* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.nav-list {
  height: 10vh;
  display: flex;
  width: 60%;
  justify-content: space-around;
  align-items: center;
  margin-left: auto;
  list-style: none;
}

.nav-links {
  text-decoration: none;
  font-size: 18px;
}

.home,
.about,
.contact {
  height: 90vh;
  width: 100%;
  position: absolute;
}

.home {
  background-image: linear-gradient(to top, #9795f0 0%, #fbc8d4 100%);
}

.about {
  background-image: linear-gradient(to top, #c1dfc4 0%, #deecdd 100%);
}

.contact {
  background-image: linear-gradient(to top, #feada6 0%, #f5efef 100%);
}

.home-content,
.about-content,
.contact-content {
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  img {
    height: 70%;
  }
  h1 {
    font-size: 50px;
    margin: 30px 0;
  }
  p {
    font-size: 20px;
  }
}
